<template>
  <div id="app">
    <el-container>
      <el-header>
        <el-row>
          <el-col :span="14">
            <div style="text-align: left;">
              <span style="font-size:large;">
                Codex
                <sup>+</sup>
              </span>
            </div>
          </el-col>
          <el-col :span="3">
            <div class="lang_box">
              <div class="lang_header" @click="clickFlag">
                <img
                  :src="'static/img/' + $i18n.locale + '.png'"
                  style="height: 16px;"
                  :alt="$i18n.locale"
                />
              </div>
            </div>
          </el-col>
          <el-col :span="userAuth.name?6:7">
            <div class="right">
              <div id="login">
                <div v-if="userAuth.name">{{userAuth.name}}</div>
                <div v-else>
                  <el-button round @click="login">{{$t('登陆')}}</el-button>
                </div>
              </div>
            </div>
          </el-col>
          <el-col :span="userAuth.name?1:0">
            <div>
              <div id="logout">
                <div v-if="userAuth.name">
                  <i class="el-icon-delete" @click="logout"></i>
                </div>
              </div>
            </div>
          </el-col>
        </el-row>
      </el-header>
      <el-main>
        <div>
          <el-alert center style="margin-bottom:15px;" type="warning">{{$t('不了解codex机制的用户请谨慎尝试')}}</el-alert>
        </div>

        <div v-loading="loading">
          <el-tabs type="border-card" stretch v-model="func" @tab-click="changeFunc">
            <!-- 兑换 -->
            <el-tab-pane :label="$t('兑换')" name="swap">
              <div class="transfer">
                <div style="margin-top: 15px;">
                  <el-row>
                    <el-col :span="16">
                      <el-input
                        size="small"
                        v-model="input_amount"
                        type="number"
                        @input="inputCalculate"
                      >
                        <template slot="prepend">{{$t('输入')}}</template>
                      </el-input>
                    </el-col>
                    <el-col :span="8">
                      <el-select
                        v-model="inputToken.symbol"
                        size="small"
                        filterable
                        @change="changeInputToken"
                      >
                        <el-option
                          v-for="item in tokenOptions"
                          :key="item.key"
                          :label="item.symbol"
                          :value="item"
                        >{{item.symbol}}</el-option>
                      </el-select>
                    </el-col>
                  </el-row>
                </div>
                <!-- 余额 -->
                <div class="right" style="margin-top: 5px;height:20px;">
                  <img :src="inputToken.logo" style="height:80%;" />
                  | {{$t('余额')}}:
                  <strong>{{inputToken.balance}} {{inputToken.symbol}}</strong>
                  @{{inputToken.contract}}
                </div>
                <div style="margin-top: 15px;">
                  <el-row>
                    <el-col :span="16">
                      <el-input
                        size="small"
                        placeholder="0"
                        v-model="output_amount"
                        type="number"
                        disabled="true"
                      >
                        <template slot="prepend">{{$t('输出')}}</template>
                      </el-input>
                    </el-col>
                    <el-col :span="8">
                      <el-select
                        v-model="outputToken.symbol"
                        size="small"
                        filterable
                        @change="changeOutputToken"
                      >
                        <el-option
                          v-for="item in outputTokenOptions"
                          :key="item.key"
                          :label="item.symbol"
                          :value="item"
                        >{{item.symbol}}</el-option>
                      </el-select>
                    </el-col>
                  </el-row>
                </div>
                <!-- 余额 -->
                <div
                  v-if="outputToken.balance!=null"
                  class="right"
                  style="margin-top: 5px;height:20px;"
                >
                  <img :src="outputToken.logo" style="height:80%;" />
                  | {{$t('余额')}}:
                  <strong>{{outputToken.balance}} {{outputToken.symbol}}</strong>
                  @{{outputToken.contract}}
                </div>
                <!-- 价格反转 -->
                <!-- <div style="margin-top: 15px;margin-left: 20px;text-align: left;">
                  <el-button size="mini" type="primary" icon="el-icon-sort" circle></el-button>
                </div>-->
                <!-- 价格展现 -->
                <div style="margin-top: 15px;">
                  <el-row>
                    <el-col :span="2">
                      <!-- <el-tooltip class="item" effect="dark" :content="$t('价格显示反转')" placement="top-start">
                      </el-tooltip>-->
                      <el-button
                        size="mini"
                        type="primary"
                        icon="el-icon-sort"
                        circle
                        style="margin-top: 0px;"
                        @click="convertPrice"
                      ></el-button>
                    </el-col>
                    <el-col :span="7">
                      <p>{{$t('当前价格')}}</p>
                      <div style="margin-top: 5px;">
                        <el-tag>{{priceDirection?formatPrice(currentPrice):formatPrice(currentPrice?(1/currentPrice):null)}}</el-tag>
                      </div>
                      <div style="margin-top: 5px;">
                        <span v-if="priceDirection">{{outputToken.symbol}} / {{inputToken.symbol}}</span>
                        <span v-else>{{inputToken.symbol}} / {{outputToken.symbol}}</span>
                      </div>
                    </el-col>
                    <el-col :span="7">
                      <p>{{$t('预估成交均价')}}</p>
                      <div style="margin-top: 5px;">
                        <el-tag
                          type="success"
                        >{{priceDirection?formatPrice(averagePrice):(averagePrice?formatPrice(1/averagePrice):null)}}</el-tag>
                      </div>
                      <div style="margin-top: 5px;">
                        <span v-if="priceDirection">{{outputToken.symbol}} / {{inputToken.symbol}}</span>
                        <span v-else>{{inputToken.symbol}} / {{outputToken.symbol}}</span>
                      </div>
                    </el-col>
                    <el-col :span="8">
                      <p>{{$t('预估成交后价格')}}</p>
                      <div style="margin-top: 5px;">
                        <el-tag
                          :type="priceWarning?'danger':'info'"
                        >{{priceDirection?formatPrice(afterPrice):formatPrice(afterPrice?(1/afterPrice):null)}}{{priceChange?` ${(priceChange*100).toFixed(1)}%`:null}}</el-tag>
                      </div>
                      <div style="margin-top: 5px;">
                        <span v-if="priceDirection">{{outputToken.symbol}} / {{inputToken.symbol}}</span>
                        <span v-else>{{inputToken.symbol}} / {{outputToken.symbol}}</span>
                      </div>
                    </el-col>
                  </el-row>
                </div>
              </div>
            </el-tab-pane>
            <!-- 资金池交易 -->
            <el-tab-pane :label="$t('资金池')" name="liquidity">
              <el-tabs v-model="lp_func" stretch @tab-click="changeLpFunc">
                <el-tab-pane :label="$t('存入')" name="deposit">
                  <div style="margin-top: 15px;">
                    <el-row>
                      <el-col :span="16">
                        <el-input
                          size="small"
                          v-model="input_amount"
                          type="number"
                          @input="depositCalculate"
                        >
                          <template slot="prepend">{{$t('输入')}}</template>
                        </el-input>
                      </el-col>
                      <el-col :span="8">
                        <el-select
                          v-model="depositToken.symbol"
                          size="small"
                          filterable
                          @change="changeDepositToken"
                        >
                          <el-option
                            v-for="item in depositTokenOptions"
                            :key="item.key"
                            :label="item.symbol"
                            :value="item"
                          ></el-option>
                        </el-select>
                      </el-col>
                    </el-row>
                    <!-- 余额 -->
                    <div
                      v-if="depositToken.balance!=null"
                      class="right"
                      style="margin-top: 5px;height:20px;"
                    >
                      <img :src="depositToken.logo" style="height:80%;" />
                      | {{$t('余额')}}:
                      <strong>{{depositToken.balance}} {{depositToken.symbol}}</strong>
                      @{{depositToken.contract}}
                    </div>
                    <!-- lp输出 -->
                    <div style="margin-top: 15px;">
                      <el-input disabled="true" size="small" v-model="output_amount" type="number">
                        <template slot="prepend">{{$t('输出')}}</template>
                        <template slot="append">{{lpToken.symbol}}</template>
                      </el-input>
                    </div>
                    <!-- 余额 -->
                    <div
                      v-if="outputToken.balance>0"
                      class="right"
                      style="margin-top: 5px;height:20px;"
                    >
                      <img :src="outputToken.logo" style="height:80%;" />
                      | {{$t('余额')}}:
                      <strong>{{outputToken.balance}} {{outputToken.symbol}}</strong>
                      @{{outputToken.contract}}
                    </div>
                  </div>
                </el-tab-pane>
                <el-tab-pane :label="$t('取出')" name="withdraw">
                  <div style="margin-top: 15px;">
                    <el-row>
                      <el-col :span="16">
                        <el-input
                          size="small"
                          v-model="input_amount"
                          type="number"
                          @input="withdrawCalculate"
                        >
                          <template slot="prepend">{{$t('输入')}}</template>
                        </el-input>
                      </el-col>
                      <el-col :span="8">
                        <el-select
                          v-model="lpToken.symbol"
                          size="small"
                          filterable
                          @change="changeDepositToken"
                        >
                          <el-option
                            v-for="item in depositTokenOptions"
                            :key="item.key"
                            :label="item.lp.symbol"
                            :value="item"
                          ></el-option>
                        </el-select>
                      </el-col>
                    </el-row>
                    <!-- 余额 -->
                    <div
                      v-if="lpToken.balance!=null"
                      class="right"
                      style="margin-top: 5px;height:20px;"
                    >
                      <img :src="lpToken.logo" style="height:80%;" />
                      | {{$t('余额')}}:
                      <strong>{{lpToken.balance}} {{lpToken.symbol}}</strong>
                      @{{lpToken.contract}}
                    </div>
                    <!-- withdraw -->
                    <el-row style="margin-top: 15px;">
                      <el-col :span="12">
                        <p>{{$t('预期赎回')}} EIDOS</p>
                        <div style="margin-top: 5px;">
                          <el-tag>{{formatPrice(withdrawEidos)}}</el-tag>
                        </div>
                      </el-col>
                      <el-col :span="12">
                        <p>{{$t('预期赎回')}} {{depositToken.symbol}}</p>
                        <div style="margin-top: 5px;">
                          <el-tag type="success">{{formatPrice(withdrawToken)}}</el-tag>
                        </div>
                      </el-col>
                    </el-row>
                  </div>
                </el-tab-pane>
              </el-tabs>
            </el-tab-pane>
          </el-tabs>
        </div>
        <el-divider>{{$t('资源费')}}</el-divider>
        <el-radio-group v-model="fee" size="mini" @change="changeFee">
          <el-radio-button border label="0">{{$t('自身资源')}} : {{$t('免费')}}</el-radio-button>
          <el-radio-button border label="2">{{$t('CPU代付')}} : 0.05 EOS</el-radio-button>
        </el-radio-group>
        <div style="margin-top: 5px;" v-if="eosBalance">
          EOS {{$t('余额')}}:
          <strong>{{eosBalance}}</strong>
        </div>
        <div style="margin-top: 5px;" v-if="cpuUsage">
          CPU {{$t('使用率')}}:
          <strong>{{(cpuUsage*100).toFixed(0)}}%</strong>
        </div>
        <div style="margin-top: 15px;">
          <el-alert center type="info">{{$t('使用EasyPay提供的CPU代付服务,支付小额手续费即可免CPU发起交易')}}</el-alert>
        </div>
        <div v-if="txid">
          <el-divider>{{$t('交易ID')}}</el-divider>
          <el-link type="success" :href="browserUrl" target="_blank">{{txid.substr(0,20)}}...</el-link>
        </div>
        <div v-if="errorMsg">
          <el-divider>{{$t('错误信息')}}</el-divider>
          <div class="flexx">
            <el-alert center :closable="false" type="error">{{errorMsg}}</el-alert>
          </div>
        </div>
        <!-- <el-divider></el-divider> -->
        <div style="margin-top: 30px;">
          <el-button type="primary" @click="send">{{$t('发送交易')}}</el-button>
        </div>
      </el-main>
      <el-footer height="40px">
        {{$t('本应用由')}}
        <span class="lichang">
          <a href="http://lichang.io">{{$t('力场 lichang.io')}}</a>
        </span>
        {{$t('孵化')}} |
        <span class="mail">
          <a href="mailto:x@qsx.io">{{$t('问题反馈')}} |</a>
        </span>
        <el-button type="text" @click="donate" class="donate">{{$t('捐赠')}}</el-button>
      </el-footer>
    </el-container>
  </div>
</template>

<script>
export default {
  name: "App",
  created() {
    // get lang from localStorage
    if (localStorage.getItem("language")) {
      this.$i18n.locale = localStorage.getItem("language");
    } else {
      localStorage.setItem("language", this.$i18n.locale);
    }
    for (const i in this.lang.all) {
      if (this.$i18n.locale == this.lang.all[i]) {
        this.lang.now = Number(i);
      }
    }
    this.outputTokenOptions = this.codex.getOutputOptions();
    this.depositTokenOptions = this.codex.getDepositLpOptions();
    // auto login
    setTimeout(this.login, 2000);
    // set refresh
    setInterval(() => {
      if (this.userAuth.name) {
        this.refreshEosBalance();
      }
      if (this.func == "swap") {
        this.getInputBalance(false);
        if (this.outputToken.symbol) {
          this.changeOutputToken(this.outputToken, 1).then(() => {
            if (this.input_amount) {
              this.inputCalculate(this.input_amount);
            }
          });
        }
      } else if (this.func == "liquidity") {
        if (this.depositToken.contract) {
          if (this.lp_func == "deposit") {
            this.getDepositBalance(false);
            if (this.outputToken.contract) {
              this.refreshOutputBalance();
            }
          } else if (this.lp_func == "withdraw") {
            this.getWithdrawBalance(false);
          }
        }
      }
    }, 10000);
  },
  mounted() {},
  data() {
    return {
      lang: {
        now: 0,
        all: ["zh", "en"]
      },
      userAuth: { name: null },
      loading: false,
      func: "swap",
      lp_func: "deposit",
      eosBalance: null,
      cpuUsage: null,
      inputToken: Object.assign({}, this.codex.tokenOptions[0]),
      outputToken: {
        symbol: null,
        balance: null,
        contract: null,
        precision: 4
      },
      depositToken: {
        symbol: null,
        balance: null,
        contract: null,
        precision: 4,
        lp: {
          symbol: null,
          balance: null,
          contract: null,
          precision: 4
        }
      },
      lpToken: {
        symbol: null,
        balance: null,
        contract: null,
        precision: 4
      },
      to: "",
      amount: 0,
      input_amount: null,
      output_amount: null,
      eidosPoolBalance: 0,
      tokenPoolBalance: 0,
      lpIssued: 0,
      withdrawToken: 0,
      withdrawEidos: 0,
      currentPrice: null,
      averagePrice: null,
      afterPrice: null,
      priceChange: null,
      priceWarning: false,
      priceDirection: true,
      memo: "",
      txid: null,
      errorMsg: null,
      browserUrl: "https://bloks.io",
      fee: "0",
      tokenOptions: Object.assign([], this.codex.tokenOptions),
      outputTokenOptions: [],
      depositTokenOptions: []
    };
  },
  methods: {
    login() {
      this.chainApi
        .connectIdentity()
        .then(res => {
          this.userAuth = res;
          this.getInputBalance();
          this.refreshEosBalance();
        })
        .catch(err => {
          this.$message.error(this.$t(err.toString()));
        });
    },
    logout() {
      this.inputToken = {
        symbol: "EOS",
        balance: null,
        contract: "eosio.token",
        precision: 4
      };
      this.eosBalance = null;
      this.chainApi.changeIdentity().then(() => {
        this.userAuth = { name: null };
      });
    },
    clickFlag() {
      // this.lang.show = !this.lang.show;
      this.lang.now += 1;
      if (this.lang.now >= this.lang.all.length) {
        this.lang.now = 0;
      }
      this.$i18n.locale = this.lang.all[this.lang.now];
      localStorage.setItem("language", this.$i18n.locale);
      // console.log(this.lang.show);
    },
    changLang(item) {
      // console.log(item);
      this.$i18n.locale = item;
      localStorage.setItem("language", item);
      this.lang.show = !this.lang.show;
    },
    async checkUser() {
      if (this.userAuth && this.to == this.userAuth.name) {
        this.$message.error(this.$t("不能转账给自己"));
        return;
      }
      if (this.to && this.to != "") {
        try {
          await this.chainApi.checkAccountExist(this.to);
        } catch (err) {
          this.$message.error(this.$t(err.toString()));
        }
      }
    },
    async getInputBalance(loading = true) {
      if (loading) {
        this.loading = true;
      }
      const account = this.userAuth.name;
      const { contract, symbol } = this.inputToken;
      try {
        const bal = await this.chainApi.getBalance(contract, account, symbol);
        this.inputToken.balance = bal;
        // if (contract == "eosio.token" && symbol == "EOS") {
        //   this.eosBalance = bal;
        // }
      } catch (err) {
        console.error(err);
        this.$message.error(this.$t("获取余额失败"));
      } finally {
        if (loading) {
          this.loading = false;
        }
      }
    },
    async getDepositBalance(loading = true) {
      if (loading) {
        this.loading = true;
      }
      const account = this.userAuth.name;
      const { contract, symbol } = this.depositToken;
      try {
        const bal = await this.chainApi.getBalance(contract, account, symbol);
        this.depositToken.balance = bal;
        this.refreshLp().then(() => {
          if (this.input_amount) {
            this.depositCalculate(this.input_amount);
          }
        });
      } catch (err) {
        console.error(err);
        this.$message.error(this.$t("获取余额失败"));
      } finally {
        if (loading) {
          this.loading = false;
        }
      }
    },
    async getWithdrawBalance(loading = true) {
      if (loading) {
        this.loading = true;
      }
      const account = this.userAuth.name;
      const { contract, symbol } = this.lpToken;
      try {
        const bal = await this.chainApi.getBalance(contract, account, symbol);
        this.lpToken.balance = bal;
        this.refreshLp().then(() => {
          if (this.input_amount) {
            this.withdrawCalculate(this.input_amount);
          }
        });
      } catch (err) {
        console.error(err);
        this.$message.error(this.$t("获取余额失败"));
      } finally {
        if (loading) {
          this.loading = false;
        }
      }
    },
    async refreshLp() {
      return Promise.all([
        this.chainApi.getBalance(
          "eidosonecoin",
          this.lpToken.contract,
          "EIDOS"
        ),
        this.chainApi.getBalance(
          this.lpToken.token.contract,
          this.lpToken.contract,
          this.lpToken.token.symbol
        ),
        this.chainApi.getTokenIssued(this.lpToken.contract, this.lpToken.symbol)
      ]).then(([eidos, token, lp]) => {
        this.eidosPoolBalance = eidos;
        this.tokenPoolBalance = token;
        this.lpIssued = lp;
      });
    },
    async refreshOutputBalance() {
      const account = this.userAuth.name;
      const { contract, symbol } = this.outputToken;
      try {
        const bal = await this.chainApi.getBalance(contract, account, symbol);
        this.outputToken.balance = bal;
        console.log({ outputToken: this.outputToken });
      } catch (err) {
        console.error(err);
        // this.$message.error(this.$t("获取余额失败"));
      }
    },
    async refreshEosBalance() {
      const account = this.userAuth.name;
      this.chainApi.getAccount(account).then(res => {
        this.eosBalance = res.core_liquid_balance;
        this.cpuUsage = res.cpu_limit.max
          ? Number(res.cpu_limit.used / res.cpu_limit.max)
          : null;
      });
    },
    changeInputToken(token) {
      // console.log(token);
      const { symbol, balance, contract, precision, logo } = token;
      this.inputToken = { symbol, balance, contract, precision, logo };
      this.getInputBalance();
      this.outputToken = {};
      this.outputTokenOptions = this.codex.getOutputOptions(token);
      this.clear();
    },
    changeLpToken(token) {
      this.lpToken = Object.assign({}, token);
    },
    changeDepositToken(token) {
      this.clear();
      const { symbol, contract, precision, logo, lp } = token;
      this.depositToken = { symbol, contract, precision, logo, lp };
      // lpToken
      const temp = Object.assign({}, lp);
      temp.token = Object.assign({}, { symbol, contract, precision, logo });
      this.changeLpToken(temp);
      // to
      this.to = lp.target;
      if (this.lp_func == "deposit") {
        this.getDepositBalance();
        this.outputToken = {
          symbol: lp.symbol,
          contract: lp.contract,
          precision: lp.precision,
          logo,
          balance: 0
        };
        this.refreshOutputBalance();
      } else if ((this.lp_func = "withdraw")) {
        this.getWithdrawBalance();
      }
    },
    async changeOutputToken(token, refresh) {
      if (!refresh) {
        this.loading = true;
        this.clear();
      }
      const { symbol, balance, contract, precision, logo } = token;
      this.outputToken = { symbol, balance, contract, precision, logo };
      this.refreshOutputBalance();
      // set target account
      try {
        const {
          to,
          currentPrice,
          calculate
        } = await this.codex.getTransferTarget(this.inputToken, token);
        this.to = to;
        this.currentPrice = currentPrice;
        this.calculate = calculate;
      } finally {
        this.loading = false;
      }
    },
    inputCalculate(amount) {
      if (this.calculate) {
        const { out, averagePrice, afterPrice } = this.calculate(amount);
        this.output_amount = out.toFixed(this.outputToken.precision);
        this.averagePrice = averagePrice;
        this.afterPrice = afterPrice;
        this.priceChange = Number(
          Math.abs(this.afterPrice - this.currentPrice) / this.currentPrice
        );
        // console.log(this.priceChange);
        if (this.priceChange > 0.1) {
          this.priceWarning = true;
        } else {
          this.priceWarning = false;
        }
      }
    },
    depositCalculate(amount) {
      if (this.lpIssued) {
        const newLp = this.codex.calculateNewLp(
          false,
          this.input_amount,
          this.eidosPoolBalance,
          this.tokenPoolBalance,
          this.lpIssued
        );
        this.output_amount = newLp.toFixed(4);
      }
    },
    withdrawCalculate(amount) {
      if (this.lpIssued) {
        const { token, eidos } = this.codex.caculateLpWithdraw(
          amount,
          this.eidosPoolBalance,
          this.tokenPoolBalance,
          this.lpIssued
        );
        this.withdrawToken = token;
        this.withdrawEidos = eidos;
      }
    },
    changeFee(mode) {
      this.fee = mode;
    },
    changeFunc() {
      this.clear();
    },
    changeLpFunc() {
      this.input_amount = null;
      if (this.depositToken.contract) {
        if (this.lp_func == "deposit") {
          this.getDepositBalance();
        } else if (this.lp_func == "withdraw") {
          this.getWithdrawBalance();
        }
      }
    },
    async send() {
      if (!this.userAuth.name) {
        this.$message.error(this.$t("请登陆"));
        return;
      }
      this.txid = null;
      this.errorMsg = null;
      this.loading = true;
      let txs;
      try {
        switch (this.func) {
          case "swap":
            txs = await this.chainApi.transfer(
              this.inputToken.contract,
              this.userAuth,
              this.to,
              Number(this.input_amount),
              this.inputToken.precision,
              this.inputToken.symbol,
              this.memo,
              this.fee
            );
            break;
          case "liquidity":
            switch (this.lp_func) {
              case "deposit":
                txs = await this.chainApi.transfer(
                  this.depositToken.contract,
                  this.userAuth,
                  this.to,
                  Number(this.input_amount),
                  this.depositToken.precision,
                  this.depositToken.symbol,
                  this.memo,
                  this.fee
                );
                break;
              case "withdraw":
                txs = await this.chainApi.transfer(
                  this.lpToken.contract,
                  this.userAuth,
                  this.to,
                  Number(this.input_amount),
                  this.lpToken.precision,
                  this.lpToken.symbol,
                  this.memo,
                  this.fee
                );
                break;
            }
            break;
          default:
            throw "wrong action";
        }

        if (this.fee == 0) {
          // use self resource
          let { originTx } = txs;
          this.txid = originTx.transaction_id;
          this.browserUrl = `https://bloks.io/transaction/${this.txid}`;
          this.$message.success(this.$t("交易成功"));
          this.loading = false;
        } else {
          // use easy pay
          let { originTx, feeTx } = txs;

          // sign originTx
          const signedOriginTx = await this.signTx(originTx, "交易确认");
          // sign feeTx
          const signedFeeTx = await this.signTx(feeTx, "支付手续费");

          const res = await this.chainApi.exec(signedOriginTx, signedFeeTx);
          this.txid = res.transaction_id;
          this.browserUrl = `https://bloks.io/transaction/${this.txid}`;
          this.$message.success(this.$t("交易成功"));
        }
        // setTimeout(() => {
        //   this.refreshEosBalance();
        //   if (this.func == "swap") {
        //     this.getInputBalance(false);
        //   } else if (this.func == "liquidity") {
        //     this.getDepositBalance(false);
        //   }
        //   this.refreshOutputBalance();
        // }, 2000);
      } catch (err) {
        console.error(err);
        debugger;
        let errMsg;
        if (err.alert) {
          errMsg = err.alert;
          this.errorMsg = err.error;
        } else if (err.message) {
          this.errorMsg = err.message;
        } else {
          errMsg = err.toString();
        }
        if (errMsg.match("交易可能失败")) {
          this.$message.error({
            message: this.$t(errMsg),
            duration: 0,
            showClose: true
          });
        } else {
          this.$message.error(this.$t(errMsg));
        }
      } finally {
        this.loading = false;
      }
    },
    async signTx(tx, message) {
      const msg = this.$message.info({
        message: this.$t(message),
        duration: 0,
        center: true
      });
      let signedTx;
      try {
        if (!!window.imToken) {
          signedTx = await this.chainApi.signAndComfirm(tx, this.userAuth);
        } else {
          signedTx = await this.chainApi.sign(tx);
        }
      } catch (err) {
        throw "拒绝交易";
      } finally {
        msg.close();
      }

      const txSigs = signedTx.transaction.signatures;
      // console.log(txSigs);
      if (txSigs.length <= 1) {
        throw "拒绝交易";
      } else if (txSigs[1].toString().substr(0, 3) != "SIG") {
        throw "拒绝交易";
      } else {
        return signedTx;
      }
    },
    clear() {
      this.input_amount = null;
      this.output_amount = null;
      this.currentPrice = null;
      this.averagePrice = null;
      this.afterPrice = null;
      this.priceChange = null;
      this.outputToken = {};
      this.eidosPoolBalance = 0;
      this.tokenPoolBalance = 0;
      this.lpIssued = 0;
    },
    donate() {
      this.$alert(
        this.$t(
          "如果您愿意支持我们的工作, 可以往 EOS 或者 YAS 账户 codexplusdev 捐赠任何的金额"
        ),
        this.$t("捐赠"),
        {
          confirmButtonText: this.$t("确定"),
          type: "info",
          center: true
        }
      );
    },
    convertPrice() {
      this.priceDirection = !this.priceDirection;
    },
    formatPrice(price) {
      if (!price) {
        return null;
      }
      const parts = price.toFixed(6).split(".");
      const intLen = parts[0].length;
      if (intLen >= 6) {
        return parts[0];
      } else {
        return `${parts[0]}.${parts[1].slice(0, 6 - intLen)}`;
      }
    }
  }
};
</script>

