import chainApi from './chainApi'

export default {
    tokenOptions: [
        {
            contract: "eidosonecoin",
            symbol: "EIDOS",
            precision: 4,
            logo:
                "https://www.api.bloks.io/image-proxy/display?w=70&h=70&url=https://ndi.340wan.com/eos/yastokencode-yas.png&op=resize"
        },
        {
            contract: "eosio.token",
            symbol: "EOS",
            precision: 4,
            lp: { contract: "eospool.eo", symbol: 'EOSLP', precision: 4, target: "eoslp.eo" },
            logo: "https://bloks.io/img/chains/eos.png"
        },
        {
            contract: "tethertether",
            symbol: "USDT",
            precision: 4,
            lp: { contract: "usdpool.eo", symbol: 'USDLP', precision: 4, target: "usdlp.eo" },
            logo:
                "https://www.api.bloks.io/image-proxy/display?w=100&h=100&url=https://raw.githubusercontent.com/eoscafe/eos-airdrops/master/logos/USDT.png&op=resize"
        },
        {
            contract: "yastokencode",
            symbol: "YAS",
            precision: 4,
            lp: { contract: "yaspool.eo", symbol: 'YASLP', precision: 4, target: "yaslp.eo" },
            logo:
                "https://www.api.bloks.io/image-proxy/display?w=70&h=70&url=https://ndi.340wan.com/eos/yastokencode-yas.png&op=resize"
        },
        {
            contract: "mkstaketoken",
            symbol: "KEY",
            precision: 4,
            lp: { contract: "keypool.eo", symbol: 'KEYLP', precision: 4, target: "keylp.eo" },
            logo:
                "https://www.api.bloks.io/image-proxy/display?w=50&h=50&url=https://raw.githubusercontent.com/BlockABC/eos-tokens/master/tokens/mkstaketoken/KEY.png&op=resize"
        },
        {
            contract: "freeamttoken",
            symbol: "AMT",
            precision: 4,
            lp: { contract: "amtpool.eo", symbol: 'AMTLP', precision: 4, target: "amtlp.eo" },
            logo:
                "https://www.api.bloks.io/image-proxy/display?w=70&h=70&url=https://ndi.340wan.com/eos/yastokencode-yas.png&op=resize"
        }
    ],
    targetMap: {
        "EIDOS/EOS": ["eos.eo", "eospool.eo"],
        "EIDOS/USDT": ["usd.eo", "usdpool.eo"],
        "EIDOS/YAS": ["yas.eo", "yaspool.eo"],
        "EIDOS/KEY": ["key.eo", "keypool.eo"],
        "EIDOS/AMT": ["amt.eo", "amtpool.eo"],
        "USDT/EOS": ["eosusd.eo"],
        "USDT/YAS": ["yasusd.eo"],
        "EOS/YAS": ["yaseos.eo"],
        "USDT/KEY": ["keyusd.eo"],
        "EOS/KEY": ["keyeos.eo"],
        "USDT/AMT": ["amtusd.eo"],
        "EOS/AMT": ["amteos.eo"]
    },
    getOutputOptions: function (input) {
        if (!input) {
            input = this.tokenOptions[0]
        }
        //设置兑出选项
        const outputTokenOptions = [];
        for (const option of this.tokenOptions) {
            if (
                option.symbol != input.symbol &&
                (["USDT", "EOS", "EIDOS"].includes(input.symbol) ||
                    ["USDT", "EOS", "EIDOS"].includes(option.symbol))
            ) {
                outputTokenOptions.push(option);
            }
        }
        return outputTokenOptions
    },
    getTransferTarget: async function (input, output) {
        if (input.symbol == 'EIDOS' || output.symbol == 'EIDOS') {
            const key = input.symbol == 'EIDOS' ? `${input.symbol}/${output.symbol}` : `${output.symbol}/${input.symbol}`
            const target = this.targetMap[key]
            const to = target[0]
            const pool = target[1]
            const [inPoolBalance, outPoolBalance] = await Promise.all([chainApi.getBalance(input.contract, pool, input.symbol),
            chainApi.getBalance(output.contract, pool, output.symbol)])
            const currentPrice = Number((outPoolBalance / inPoolBalance).toFixed(6));
            const calculate = function (amount) {
                const _in = output.symbol == 'EIDOS' ? Number(amount) : Number(amount) * 0.99
                const out = outPoolBalance - ((inPoolBalance * outPoolBalance) / (_in + inPoolBalance))
                const averagePrice = _in == 0 ? null : Number((out / _in).toFixed(6));
                const afterPrice = Number(((outPoolBalance - out) / (inPoolBalance + Number(amount))).toFixed(6));
                return { out, averagePrice, afterPrice }
            }
            return { to, currentPrice, calculate }
        } else {
            // cascaded transaction
            // get transfer target 
            const target = this.targetMap[`${input.symbol}/${output.symbol}`] ?
                this.targetMap[`${input.symbol}/${output.symbol}`] :
                this.targetMap[`${output.symbol}/${input.symbol}`]
            const to = target[0]
            // get calcalate pool
            const inTarget = this.targetMap[`EIDOS/${input.symbol}`]
            const outTarget = this.targetMap[`EIDOS/${output.symbol}`]
            const inPool = inTarget[1]
            const outPool = outTarget[1]
            const [inPoolTokenBalance, inPoolEidosBalance,
                outPoolTokenBalance, outPoolEidosBalance]
                = await Promise.all([
                    chainApi.getBalance(input.contract, inPool, input.symbol),
                    chainApi.getBalance('eidosonecoin', inPool, 'EIDOS'),
                    chainApi.getBalance(output.contract, outPool, output.symbol),
                    chainApi.getBalance('eidosonecoin', outPool, 'EIDOS'),
                ])
            const currentPrice = Number((outPoolTokenBalance * inPoolEidosBalance / outPoolEidosBalance / inPoolTokenBalance).toFixed(6));
            const calculate = function (amount) {
                const _in = Number(amount)
                const cascaded = inPoolEidosBalance - ((inPoolTokenBalance * inPoolEidosBalance) / (_in + inPoolTokenBalance))
                const out = outPoolTokenBalance - ((outPoolEidosBalance * outPoolTokenBalance) / (cascaded * 0.99 + outPoolEidosBalance))
                const averagePrice = _in == 0 ? null : Number((out / _in).toFixed(6));
                const afterPrice = Number(
                    (
                        (outPoolTokenBalance - out)
                        * (inPoolEidosBalance - cascaded)
                        / (outPoolEidosBalance + cascaded)
                        / (inPoolTokenBalance + _in)
                    ).toFixed(6));
                return { out, averagePrice, afterPrice }
            }
            return { to, currentPrice, calculate }
        }
    },
    getDepositLpOptions: function () {
        return this.tokenOptions.slice(1)
    },
    calculateNewLp(direction, received, eidos, token, lp) {
        // direction 1:send eidos
        // direction 0: send token
        received = Number(received)
        eidos = Number(eidos)
        token = Number(token)
        lp = Number(lp)
        if (direction) {
            const a = token;
            const b = (1.99 * token * eidos) - (0.01 * token * received);
            const c = - received * token * eidos;

            const token_part = (Math.sqrt((b * b) - (4 * a * c)) - b) / (2 * a);
            const eidos_part = received - token_part;

            const new_shares = lp * (eidos_part / (eidos + token_part));
            return new_shares
        } else {
            const a = eidos;
            const b = 2 * eidos * token;
            const c = - received * eidos * token;

            const eidos_part = (Math.sqrt((b * b) - (4 * a * c)) - b) / (2 * a);
            const token_part = received - eidos_part;

            const new_shares = lp * (token_part / (token + eidos_part));
            return new_shares
        }

    },
    caculateLpWithdraw(received, eidos, token, eoslp) {
        const eidos_portion = eidos * (received / eoslp);
        const token_portion = token * (received / eoslp);
        return {
            token: token_portion,
            eidos: eidos_portion
        }
    }

}


