import Eos from "eosjs-classic";
import ecc from 'eosjs-ecc';
import axios from "axios";
// scatter
import ScatterJS from '@scatterjs/core';
import ScatterEOS from '@scatterjs/eosjs';
ScatterJS.plugins(new ScatterEOS());
let scatterMode = null
let plugin

// 环境的切换
if (process.env.NODE_ENV === "development") {
    axios.defaults.baseURL = "//api.easypayeos.com";
} else if (process.env.NODE_ENV === "production") {
    // todo
    axios.defaults.baseURL = "//api.easypayeos.com";
}

//chain const
const host = "eosmainnet.medishares.net";//"eos.eoscafeblock.com"// "a0a2a34c-f0ca-42a9-b6fa-d1866785b331.gw.eosinfra.io"; // "api.eoslaomao.com""https://eos.newdex.one";
const httpEndpoint = `https://${host}`;
const chainId =
    "aca376f206b8fc25a6ed44dbdc66547c36c6c33e3a119ffbeaef943642f0e906";

const httpNetwork = {
    host,
    blockchain: "eos",
    chainId,
    port: 443,
    protocol: "https"
};

const eosOptions = {
    broadcast: true,
    sign: true,
    chainId: chainId
};

const scatterJsNetwork = ScatterJS.Network.fromJson(httpNetwork);

const pluginName = "scatter";

const eosapi = Eos({ httpEndpoint, chainId });

const exchange_list = [
    "binancecleos", "huobideposit", "addeoswallet", "bikicoineos5",
    "bitmaxaddrz1", "chainupsaas2", "cobowalletio", "mxcexdeposit",
    "newdexpocket", "dex.io", "dexeoswallet", "whaleextrust", "btexexchange",
    "eosiochaince", "findexfindex", "pwrdbyobolus", "yolonetworkx", "bidname.com",
    "onedex123451", "mdex1wallets", "bancorc11111", "bancorc11112", "bancorc11113",
    "bancorc11114", "bancorc11121", "bancorc11122", "bancorc11124", "bancorc11125", "bancorc11132",
    "bancorc11133", "bnt2eoscnvrt", "cointotheeos", "buttonbutton", "bancordexapi", "etbexchanger",
    "tokenstore.x", "wallet4bixin", "wwweosswapio", "bitfinexleo1", "newdexpublic", "eoslocallyio", "eostokensdex",
    "bkexeosusers", "timeforestcm", "bestwintoken", "lichanginput", "okbtothemoon"
]

// exchange list
// axios.get('https://aml.slowmist.com/api/eos_exchange_contract_addr')
//     .then(res => {
//         console.log(res)
//         debugger
//     })

export default {
    getBalance: function (code, account, symbol) {
        return eosapi.getCurrencyBalance(code, account, symbol).then(bal => {
            return bal[0] ? Number(bal[0].split(" ")[0]) : 0;
        })
    },
    transfer: async function (code, user, to, amount, precision, symbol, memo, level) {
        try {
            if (Number(amount.toFixed(precision)) <= 0) {
                throw '金额格式错误'
            }
            level = Number(level)
            let feeAmount
            if (level == 0) {
                feeAmount = 0
            } else if (level == 2) {
                feeAmount = 0.05
            } else {
                feeAmount = 0.01
            }
            await this.checkAccountExist(to);

            if (!memo) {
                memo = ""
            }

            if (exchange_list.includes(to) && memo == "") {
                throw '转账到交易所需要填写memo'
            }

            // check exchanges
            // if (exchange_list.includes(to)) {
            //     throw '暂时不支持转账到交易所'
            // }

            const from = user.name;

            if (from == to) {
                throw '不能转账给自己'
            }

            const eosBalance = await this.getBalance("eosio.token", from, "EOS")
            if (code == "eosio.token" && symbol == "EOS") {
                if (eosBalance < feeAmount + amount) {
                    throw "EOS余额不够支付(转账金额+手续费)";
                }
            } else {
                if (eosBalance < feeAmount) {
                    throw "余额不够支付手续费";
                }
                const balance = await this.getBalance(code, from, symbol);
                if (balance < amount) {
                    throw "余额不足";
                }
            }

            const data = {
                from: user.name,
                to,
                quantity: `${amount.toFixed(precision)} ${symbol}`,
                memo
            }

            return this.contract(user, code, 'transfer', data, level)
        } catch (err) {
            throw err;
        }
    },
    contract: async function (user, code, action, data, level) {
        try {
            if (!code) {
                throw "请选择合约";
            }
            if (!action) {
                throw "请选择操作";
            }
            if (!data) {
                throw "请输入参数";
            }
            level = Number(level)
            let feeAmount
            if (level == 0) {
                feeAmount = 0
            } else if (level == 2) {
                feeAmount = 0.05
            } else {
                feeAmount = 0.01
            }
            const eosBalance = await this.getBalance("eosio.token", user.name, "EOS")
            if (eosBalance < feeAmount) {
                throw "余额不够支付手续费";
            }

            if (level == 0) {
                const actions = [{
                    account: code,
                    name: action,
                    authorization: [
                        { actor: user.name, permission: user.authority }
                    ],
                    data
                }]
                const originTx = await plugin.transaction({ actions }, {
                    max_cpu_usage_ms: 20
                })
                return { originTx }
            } else {
                try {
                    const { data: { originTx, feeTx } } = await axios.post("/propose", {
                        user, code, action, data, level
                    });
                    return { originTx, feeTx }
                } catch (err) {
                    console.error(err);
                    const errString = err.toString()
                    if (errString.match("tx_cpu_usage_exceeded")) {
                        throw "交易超时，请使用复杂交易";
                    } else if (err.response && err.response.data.match("symbol is a required")) {
                        throw "金额格式错误";
                    } else {
                        throw "代付请求失败";
                    }
                }
            }
        } catch (err) {
            throw err;
        }
    },
    exec: async function (originTx, feeTx) {
        try {
            const { data: txResult } = await axios.post("/execute", {
                originTx, feeTx
            });
            return txResult;
        } catch (err) {
            if (err.response.data) {
                if (err.response.data.match("tx_cpu_usage_exceeded")) {
                    throw { alert: "交易超时，请重试或请使用复杂交易", error: err.response.data }
                }
                throw { alert: '交易可能失败 ,请查询浏览器确认交易是否发生', error: err.response.data }
            } else {
                throw '交易可能失败 ,请查询浏览器确认交易是否发生'
            }
        }
    },
    sign: async function (rawTx) {

        const { signatures, transaction } = rawTx.transaction;
        const payerSig = signatures[0]

        // senderSig
        const signedTx = await plugin.transaction(transaction, {
            broadcast: false
        });
        const senderSig = signedTx.transaction.signatures[0]

        // concat 
        rawTx.transaction.signatures = [payerSig, senderSig]

        return rawTx

    },
    signAndComfirm: async function (rawTx, user) {

        const { signatures, transaction } = rawTx.transaction;
        const payerSig = signatures[0]

        const signPlugin = Eos({
            httpEndpoint, chainId, signProvider: async ({ buf, sign, transaction }) => {
                const action = transaction.actions[0]
                const JsonData = await eosapi.abiBinToJson(action.account, action.name, action.data)
                const data = JsonData.args
                const hash = ecc.sha256(buf)
                try {
                    const sig = await window.scatter.getArbitrarySignature(user.publicKey, hash, JSON.stringify({ contract: action.account, action: action.name, data }), true)
                    console.log(sig)
                    return sig
                } catch (err) {
                    console.error(err)
                }
            }
        });

        const signedTx = await signPlugin.transaction(transaction, {
            broadcast: false
        });
        const senderSig = signedTx.transaction.signatures[0]

        // concat 
        rawTx.transaction.signatures = [payerSig, senderSig]

        return rawTx

    },
    //连接插件
    connectIdentity() {
        return new Promise((resolve, reject) => {
            if (window[pluginName]) {
                scatterMode = 'old'
                console.log({ scatterMode })
                window[pluginName]
                    .getIdentity({ accounts: [httpNetwork] })
                    .then(identity => {
                        // console.log(identity)
                        if (identity.accounts.length > 0) {
                            let account = identity.accounts[0];
                            //this.accountName = account_name;
                            resolve(account);
                        } else {
                            reject("请导入EOS账号");
                        }
                    })
                    .catch(error => {
                        console.error(error);
                        reject(error);
                    });
            } else if (scatterMode == 'ScatterJS') {
                // already connect
                ScatterJS.login().then(id => {
                    if (!id) {
                        reject("请导入EOS账号");;
                    }
                    const account = ScatterJS.account('eos');
                    resolve(account);
                });
            } else {
                ScatterJS.connect('EasyPay', { network: scatterJsNetwork }).then(connected => {
                    if (!connected) {
                        reject('请使用EOS钱包或者插件打开');
                    }
                    scatterMode = 'ScatterJS'
                    console.log({ scatterMode })

                    ScatterJS.login().then(id => {
                        if (!id) {
                            reject("请导入EOS账号");;
                        }
                        const account = ScatterJS.account('eos');
                        resolve(account);
                    });
                });
                // setTimeout(() => {
                //     this.connectIdentity()
                //         .then(account => {
                //             resolve(account);
                //         })
                //         .catch(error => {
                //             reject(error);
                //         });
                // }, 2000);
            }

        }).then(res => {
            // set plugin
            if (scatterMode == 'old') {
                plugin = window[pluginName].eos(
                    httpNetwork,
                    Eos,
                    eosOptions,
                    "https"
                );
            } else if (scatterMode == 'ScatterJS') {
                plugin = ScatterJS.eos(scatterJsNetwork, Eos);
            } else {
                throw '插件错误'
            }
            return res
        })
    },
    //切换插件用户
    changeIdentity() {
        return new Promise((resolve, reject) => {
            if (scatterMode) {
                if (scatterMode == 'old') {
                    window[pluginName].forgetIdentity().then(() => {
                        resolve();
                    });
                } else if (scatterMode == 'ScatterJS') {
                    ScatterJS.logout().then(() => {
                        resolve();
                    });
                } else {
                    reject(`plugin not loaded`);
                }
            } else {
                reject(`plugin not loaded`);
            }
        });
    },
    //检查账号是否存在
    async checkAccountExist(accountName) {
        try {
            let res = await eosapi.getAccount(accountName);
            //console.log(res);
            if (res.account_name == accountName) {
                return true;
            } else {
                throw "账户不存在";
            }
        } catch (err) {
            //console.log(err);
            // return false;
            throw "账户不存在";
        }
    },
    getAccount(accountName) {
        return eosapi.getAccount(accountName);
    },
    getTokenIssued: function (code, symbol) {
        return eosapi.getCurrencyStats(code, symbol).then(lp => {
            const supply = lp[symbol].supply
            return Number(supply.split(" ")[0])
        })
    },
};



